@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  background-image: url(/static/media/background.73534c23.jpg);
  background-size: cover;
}
.header{
    
    background-size: cover;
    width:100%;
    background-color:#3c3c46;
    
}
.header-bar{
    height:70px;
    background-color:transparent;
    opacity:1;
    border-bottom: solid 1px #ffffff;
    position: relative;
}
.nav-menu  {
    /* float:right; */
    margin-left:3%;
}

.selected {
    text-decoration: none;
    color:white;
    text-align:center;
    background-color: #454cad;
    border-radius: 20px;
    transition: background-color  0.3s linear;
    
}
.nav-menu  a {
    text-decoration: none;
    color:white;
    text-align:center;
   
    padding: 6px 20px;
    
}
.nav-menu ul li{

    list-style: none;
    float: left;
    margin-right:30px;
    font-size: 24px;
    margin-top:17px;
}

.question{
    width:20%;
    height:300px;
    margin-left:3%;
    margin-top:5%;
    background-color: rgba(0,0,0,0.5);
    border-radius: 15px;
    text-align: center;
}
.question p{
    color:white;
    height:50%;
    overflow-y: auto;
    font-size: 2.5vh;
}
a{
    text-decoration: none;
}
.question button{
	font-family: 'Montserrat', Arial, Helvetica, sans-serif;
	float:left;
	width: 40%;
	border: #fbfbfb solid 1px;
	cursor:pointer;
	color:white;
	font-size:12px;
	padding-top:10px;
	padding-bottom:10px;
	transition: all 0.3s;
    margin-top:-4px;
    font-weight:700;
    margin-left: 7%;
    margin-top:15%;
}

#delete-button{
	background-color: #db3434;
}
#delete-button:hover{
	background-color: rgba(0,0,0,0);
	color: #db3434;
}

#update-button{
	background-color: #3498db;
}
#update-button:hover{
	background-color: rgba(0,0,0,0);
	color: #3498db;
}
	
#feedback-page{
	text-align:center;
}

#form-main{
	width:100%;
	float:left;
	padding-top:0px;
}

#form-div {
	background-color:rgba(72,72,72,0.4);
	padding-left:35px;
	padding-right:35px;
	padding-top:35px;
	padding-bottom:50px;
	width: 450px;
	float: left;
	left: 50%;
	position: absolute;
  margin-top:30px;
	margin-left: -260px;
  -moz-border-radius: 7px;
  -webkit-border-radius: 7px;
}

.feedback-input {
	color:#3c3c3c;
	font-family: Helvetica, Arial, sans-serif;
  font-weight:500;
	font-size: 18px;
	border-radius: 0;
	line-height: 22px;
	background-color: #fbfbfb;
	padding: 13px 13px 13px 54px;
	margin-bottom: 10px;
	width:100%;
	-ms-box-sizing: border-box;
	box-sizing: border-box;
  border: 3px solid rgba(0,0,0,0);
}

.feedback-input:focus{
	background: #fff;
	box-shadow: 0;
	border: 3px solid #3498db;
	color: #3498db;
	outline: none;
  padding: 13px 13px 13px 54px;
}

.focused{
	color:#30aed6;
	border:#30aed6 solid 3px;
}

/* Icons ---------------------------------- */
#name{
	background-image: url(http://rexkirby.com/kirbyandson/images/name.svg);
	background-size: 30px 30px;
	background-position: 11px 8px;
	background-repeat: no-repeat;
}

#name:focus{
	background-image: url(http://rexkirby.com/kirbyandson/images/name.svg);
	background-size: 30px 30px;
	background-position: 8px 5px;
  background-position: 11px 8px;
	background-repeat: no-repeat;
}

#email{
	background-image: url(http://rexkirby.com/kirbyandson/images/email.svg);
	background-size: 30px 30px;
	background-position: 11px 8px;
	background-repeat: no-repeat;
}

#email:focus{
	background-image: url(http://rexkirby.com/kirbyandson/images/email.svg);
	background-size: 30px 30px;
  background-position: 11px 8px;
	background-repeat: no-repeat;
}

#comment{
	background-image: url(http://rexkirby.com/kirbyandson/images/comment.svg);
	background-size: 30px 30px;
	background-position: 11px 8px;
	background-repeat: no-repeat;
}

textarea {
    width: 100%;
    height: 150px;
    line-height: 150%;
    resize:vertical;
}

input:hover, textarea:hover,
input:focus, textarea:focus {
	background-color:white;
}

#button-blue{
	font-family: 'Montserrat', Arial, Helvetica, sans-serif;
	float:left;
	width: 100%;
	border: #fbfbfb solid 4px;
	cursor:pointer;
	background-color: #3498db;
	color:white;
	font-size:24px;
	padding-top:22px;
	padding-bottom:22px;
	transition: all 0.3s;
  margin-top:-4px;
  font-weight:700;
}

#button-blue:hover{
	background-color: rgba(0,0,0,0);
	color: #0493bd;
}
	
.submit:hover {
	color: #3498db;
}
	
.ease {
	width: 0px;
	height: 74px;
	background-color: #fbfbfb;
	transition: .3s ease;
}

.submit:hover .ease{
  width:100%;
  background-color:white;
}

@media only screen and (max-width: 580px) {
	#form-div{
		left: 3%;
		margin-right: 3%;
		width: 88%;
		margin-left: 0;
		padding-left: 3%;
		padding-right: 3%;
	}
}
